.testimonials {
  padding: 148px 227px 168px;
  max-width: 1700px;
  margin: 0 auto;
  clear: both;

  &__caption {
    margin: 0 0 16px;
    font-weight: bold;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 3px;
    text-transform: uppercase;
    color: #c0cdd7;
  }

  &__heading {
    margin: 0 0 85px;
    font-weight: 600;
    font-size: 32px;
    line-height: 150%;
    color: $heading-color;
  }

  &__people {
    display: flex;
    justify-content: space-between;
  }

  .person:nth-child(2) .person__left-ellipse {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%,
    rgba(255, 255, 255, 0) 100%),
      #fcc304;
  }

  .person:nth-child(2) .person__right-ellipse {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%,
    rgba(255, 255, 255, 0) 100%),
      #fcc304;
  }

  .person:last-child .person__left-ellipse {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%,
    rgba(255, 255, 255, 0) 100%),
      #f25656;
  }

  .person:last-child .person__right-ellipse {
    background: linear-gradient(180deg, rgba(255, 255, 255, 0.25) 0%,
    rgba(255, 255, 255, 0) 100%),
      #f25656;
  }
}

@media screen and (max-width: 1450px) {
  .testimonials {
    padding: 148px 107px 168px;
  }
}
